.footer__container {
  padding-top: 47px;
  padding-bottom: 19px;

  @media screen and (min-width: 768px) {
    & {
      padding-top: 50px;
      padding-bottom: 21px;
    }
    @media screen and (min-width: 635px) {
      padding-bottom: 21px;
    }

    @media screen and (min-width: 1280px) {
      & {
        padding-top: 82px;
      }
    }
  }
  .footer__social-list {
    margin-bottom: 34px;
    display: flex;
    justify-content: center;

    @media screen and (min-width: 768px) {
      & {
        margin-bottom: 51px;
      }
    }

    @media screen and (min-width: 1280px) {
      & {
        margin-bottom: 35px;
      }
    }
  }

  .footer__list {
    display: flex;
    justify-content: space-between;
  }

  .footer__link {
    font-size: 16px;
    line-height: 1.25;
    transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
    font-style: normal;

    &:hover,
    &:focus {
      color: var(--focus-hover-color);
    }
  }

  .footer__icon-phone {
    width: 13px;
    height: 13px;
  }

  .footer__icon-location {
    width: 11px;
    height: 14px;
    z-index: 10000;
  }

  .footer__icon {
    margin-right: 3px;

    transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);

    &:hover,
    &:focus {
      fill: var(--focus-hover-color);
    }
  }
}
.footer__link:hover,
.footer__link:focus {
  fill: var(--focus-hover-color);
}
.footer--bg-image {
  background-image: url(/images/footer/mobile/flour_mobile.png);
  background-repeat: no-repeat;
  background-position: bottom right;

  @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
    background-image: url(/images/footer/mobile/flour_mobile@2x.png);
  }

  @media screen and (min-width: 768px) {
    & {
      background-size: contain;
      background-image: url(/images/footer/tablet/flour_tablet.png);
    }

    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url(/images/footer/tablet/flour_tablet@2x.png);
    }
  }

  @media screen and (min-width: 1280px) {
    & {
      background-image: url(/images/footer/desktop/flour_desktop.png);
    }

    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url(/images/footer/desktop/flour_desktop@2x.png);
    }
  }
}
