.contact__container {
  text-align: center;
}

.contact__text {
  margin-bottom: 23px;

  @media screen and (min-width: 1280px) {
    & {
      margin-bottom: 21px;
    }
  }
}

.contact__form {
  @media screen and (min-width: 768px) {
    & {
      max-width: 410px;
    }
  }

  @media screen and (min-width: 1280px) {
    & {
      max-width: 430px;
    }
  }
}
